    margin-block-start: 0;
    margin-block-end: 0;
    padding-inline-start: 10px; }
    nav ul .warp-manu {
      display: flex;
      flex-direction: row; }
      nav ul .warp-manu li {
        display: flex;
        padding: 3px 10px; }
        nav ul .warp-manu li p {
          color: black;
          margin: 0.5rem 0.5rem; }
        nav ul .warp-manu li p:hover {
