.warp-cover {
  width: 100%;
  display: flex;
  justify-content: center; }
  .warp-cover .warp-text {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    width: 100%;
    padding: 3rem; }
    .warp-cover .warp-text button {
      margin: 1.5rem;
      background-color: #ffe8fc;
      border: 1px solid;
      padding: 1rem;
      border-radius: 1rem; }
      .warp-cover .warp-text button .btn {
        font-size: 1.5rem; }
  .warp-cover .cover {
    width: 100%;
    border-radius: 1rem;
    display: flex;
    justify-content: center;
    margin: 1rem;
    margin-top: 0.3rem;
    flex-direction: row;
    background-image: linear-gradient(to right top, #ffe8fc, #feeafd, #f6e9ff, #f5e9ff, #f1e6ff); }
    .warp-cover .cover img {
      width: 50rem;
      margin-top: 10rem; }

.warp-bg {
  width: 100%;
  display: flex;
  margin-bottom: 1rem; }
  .warp-bg .bg {
    width: 100%;
    background: url("/static/bg3.jpg");
    background-size: cover;
    background-position: center center;
    opacity: 0.85;
    display: flex;
    flex-direction: row;
    justify-content: center; }
    .warp-bg .bg .warp-text {
      width: 50%;
      margin: 10rem;
      display: flex;
      flex-direction: column;
      align-items: center; }
      .warp-bg .bg .warp-text .btn {
        background-color: #cde4f4 !important;
        border: 1px solid #adb4bc;
        border-radius: 1rem;
        margin-top: 1rem;
        box-shadow: 3px 4px 14px -6px;
        -webkit-box-shadow: 3px 4px 14px -6px rgba(0, 0, 0, 0.75);
        -moz-box-shadow: 3px 4px 14px -6px rgba(0, 0, 0, 0.75);
        box-shadow: 3px 4px 14px -6px rgba(0, 0, 0, 0.75); }
      .warp-bg .bg .warp-text .btn:hover {
        box-shadow: 3px 4px 14px -10px; }

.warp-menu {
  display: flex;
  margin-left: 2rem;
  margin-right: 2rem; }
  .warp-menu .menu {
    width: 100%;
